






















import {Component, Prop, Vue} from "vue-property-decorator"
import emailRegEx from "@/mixins/emailRegEx"
import i18n from "../i18n"

@Component({ components: { } })
export default class UserUpdate extends Vue {
	@Prop()
	private dataType!: string

	private firstField: string = ""
	private secondField: string = ""

	public get inputType(): string {
		return this.dataType === "password" ? "password" : "text"
	}

	private label: object = {
		name: {
			title: i18n.t("userUpdates.name"),
			ph1: i18n.t("userUpdates.name1"),
			ph2: i18n.t("userUpdates.name2"),
		},
		email: {
			title: i18n.t("userUpdates.email"),
			ph1: i18n.t("userUpdates.email1"),
			ph2: i18n.t("userUpdates.email2"),
		},
		password: {
			title: i18n.t("userUpdates.pass"),
			ph1: i18n.t("userUpdates.pass1"),
			ph2: i18n.t("userUpdates.pass2"),
		}
	}

	private get isFormValid(): boolean {
		switch (this.dataType) {
			case "name":
				return this.firstField.trim().length >= 2 && this.secondField.trim().length >= 2
				break
			case "email":
				return this.isEmailValid && this.firstField.trim() === this.secondField.trim()
				break
			case "password":
				return this.firstField.trim().length >= 8 && this.firstField.trim() === this.secondField.trim()
				break
			default:
				return false
		}
	}

	private get isEmailValid(): boolean {
		return emailRegEx.test(this.firstField)
	}

	private updateData(): void {
		switch (this.dataType) {
			case "name":
				alert("name")
				break
			case "email":
				alert("email")
				break
			case "password":
				alert("password")
				break
		}
		this.$emit("close")
	}
}
